html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: 'IBM Plex Mono', monospace, sans-serif;
  margin: 0;
  font-weight: 100;
  color: #555;
}

h1 {
  font-weight: 200;
  margin: 0;
  padding: 0;
}

/* --------------------------------------------- */
/* buttons */
/* --------------------------------------------- */
#btnTop {
  display: block;
  position: fixed;
  bottom: 20px;
  right: 30px;
  z-index: 8;
  border: none;
  outline: none;
  background-color: #e67e22;
  color: #fff;
  cursor: pointer;
  padding: 14px;
  font-size: 130%;
  border-radius: 50%;
  transition: background 0.5s;
}
#btnTop:hover {
  background: #555;
}

/* --------------------------------------------- */
/* NAVBAR */
/* --------------------------------------------- */

.logo-nav {
  float: left;
  padding: 20px;
}

.logo-nav a {
  cursor: pointer;
  font-size: 2em;
}

.main-nav {
  float: right;
  padding: 20px;
}

.main-nav a {
  cursor: pointer;
  font-size: 2em;
  margin-left: 20px;
}

.main-nav a:link,
.main-nav a:visited {
  cursor: pointer;
  border-bottom: 3px solid transparent;
}

.main-nav a:hover,
.main-nav a:active {
  border-bottom: 3px dotted #e67e22;
}

.main-nav > .active {
  color: #e67e22;
  border-bottom: 3px dotted #e67e22;
}

.logo-nav > .active {
  border-bottom: 3px solid #000;
}

.sticky {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(255, 255, 255, 0.96);
  box-shadow: 0 2px 2px #d3d3d3;
  z-index: 7;
}

/* --------------------------------------------- */
/* LANDING PAGE */
/* --------------------------------------------- */
.landing-section {
  height: 100vh;
  background: #f4f4f4;
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo-big {
  width: 900px;
}

/* --------------------------------------------- */
/* ABOUT */
/* --------------------------------------------- */
.about-section {
  height: 100vh;
  background: #fff;
  text-align: center;
  position: relative;
}

.banner-text {
  color: #e8e8e8;
  background: #1d1d1d;
  width: 75%;
  margin: auto;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.banner-text h1 {
  font-size: 66px;
  font-weight: 600;
}

.banner-text h1 span {
  text-decoration: line-through;
}

.banner-text hr {
  width: 50%;
  border-top: 5px dotted #fff;
  margin: auto;
}

.banner-text p {
  font-size: 20px;
  padding-top: 20px;
  margin-bottom: 0;
}

.social-links {
  width: 50%;
  margin: auto;
  display: flex;
  justify-content: center;
}

.social-links i {
  color: #e8e8e8;
  font-size: 100px;
  padding: 20px 10px 10px 10px;
  margin-left: 25px;
  margin-right: 25px;
}

/* --------------------------------------------- */
/* PROJECTS */
/* --------------------------------------------- */
.projects-section {
  height: 100vh;
  background: #f4f4f4;
  position: relative;
}

.wrapper {
  padding: 5%;
  overflow: visible;
  width: 90%;
  margin: 60px auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-auto-rows: 210px;
  grid-row-gap: 60px;
  grid-column-gap: 30px;
}

.wrapper h3 {
  font-size: 1.1em;
  display: inline-block;
  font-weight: 300;
  border-bottom: 3px solid transparent;
  transition: border 0.5s;
  margin-bottom: 0;
}

.item a img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  margin-bottom: 3%;
  transition: transform 0.3s ease-in-out;
}

.item a img:hover {
  transform: scale(1.1);
}

.item a {
  text-decoration: none;
  color: #444;
  transition: color 0.5s;
}

.item a:hover h3 {
  color: #000;
  border-bottom: 3px solid #d35400;
}

/* --------------------------------------------- */
/* CONTACT */
/* --------------------------------------------- */
.contact-section {
  height: 100vh;
  background: #fff;
  position: relative;
}
.contact-section .map {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -40%);
}
.contact-section .map img {
  width: 60%;
}

/* --------------------------------------------- */
/* FOOTER */
/* --------------------------------------------- */
.footer-section {
  /* height: 15vh; */
  background: #444;
  color: #fafafa;
  text-align: center;
}
.footer-section p {
  margin: 0;
  padding: 30px 0;
}

.footer-section p i {
  color: #cecbcb;
}

/* --------------------------------------------- */
/* media queries */
/* --------------------------------------------- */
@media only screen and (max-width: 920px) {
  .logo-big {
    width: 50%;
  }
}

@media only screen and (max-width: 768px) {
  .contact-section .map img {
    width: 80%;
  }

  .logo-nav a {
    cursor: pointer;
    font-size: 1em;
  }

  .main-nav a {
    cursor: pointer;
    font-size: 1em;
    margin-left: 20px;
  }
}

@media only screen and (max-width: 480px) {
  .wrapper {
    width: 90%;
    margin: 60px auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-auto-rows: 210px;
    grid-row-gap: 60px;
    grid-column-gap: 30px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -70%);
  }
  .banner-text h1 {
    font-size: 35px;
    font-weight: 600;
  }
  .social-links i {
    font-size: 50px;
  }
}
